@import "partials/variables";

body {
    background-color: #eef2f7;
}

#particles-js {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#logo-tilt {
    height: 300px;
    width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform-style: preserve-3d;
    position: absolute;
    z-index: 100;
    .inner-logo-tilt {
        height: 200px;
        width: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: translateZ(20px);
        .logo {
            height: 150px;
            width: 150px;
        }
    }
}

.contact {
    position: absolute;
    background-color: #181c1e;
    top: 65%;
    width: 335px;
    h1 {
        color: #eef2f7;
        font-size: 30px;
        text-align: center;
        text-transform: uppercase;
        font-family: 'bebas';
    }
}